import { OnInit, ViewChild, Injector } from '@angular/core';
import { AlertService, SessionService } from '../../services';
import { appInjector } from '../../app.injector';
import { Router } from '@angular/router';

export abstract class BaseComponent implements OnInit {

    @ViewChild('f') htmlForm: HTMLFormElement;
    @ViewChild('appSales') appSales;

    public showList = true;

    public loading = false;
	public loading2 = false;
    protected editMode = false;
    protected editDetailMode = false;
    protected search = false;

    public data: any = [];
    protected form: any = {};
    protected filter: any = {};
    protected refresh: any;

    public dataDetail: any = [];
    public dataDetail2: any = [];
    protected formDetail: any = {};

    /**
     * Upload config
     */
    afuConfig: any = [];

    /**
     * Pagination for Master Data
     */
    protected pageSize = 10;
    protected currentPage = 1;
    protected totalItems = 0;

    protected moduleName = '';
    protected moduleForm: any = {};
    protected moduleFilter: any = {};
    protected modulePermission = '';

    protected moduleFormDetail: any = {};

    protected model: any;
    protected detailModel: any;

    /**
     * Pagination for Detail
     */
    public detailPageSize = [10];
    public detailCurrentPage = [1];
    public detailTotalItems = [0];

    public show = { back: false };

    protected session: SessionService;
    protected router: Router;

    /**
     * Constructor
     */
    constructor(private alertService: AlertService) {
        this.form = Object.assign({}, this.moduleForm);
        this.formDetail = Object.assign({}, this.moduleFormDetail);
    }

    /**
     * Component Init
     */
    ngOnInit() {
        this.clearForm();

        this.loadDependencies();

        // Set Default Filter Object
        this.filter = Object.assign({}, this.moduleFilter);

        // Set Session and Router
        const injector: Injector = appInjector();

        this.session = injector.get(SessionService);
        this.router = injector.get(Router);

        const filterSession = this.session.getSession('filters', false);

        if (filterSession) {
            // tslint:disable-next-line: forin
            for (const key in filterSession) {
                this.filter[key] = filterSession[key];
            }

            this.show.back = true;
        }

        // Load Data
        this.loadData(this.currentPage);
    }

    /**
     * Save / Update Data
     *
     * @param value
     * @param valid
     *
     */
    saveData(value, valid): void {

        if (this.loading) {
            this.alertService.error(' processing, please wait!');

            return;
        }

        if (this.detailModel && this.dataDetail.length < 1) {
            this.alertService.error('Minimal harus ada 1 data di tabel detail');

            return;
        }

        if (!this.validate()) {
            return;
        }

       this.loading = true;

        if (this.detailModel) {
            this.form.dataDetail = this.dataDetail;
        }

        this.onBeforeSave();


        if (this.editMode) {

            this.model.update(this.form, this.form.id)
                .subscribe(
                    (res: any) => {
                        this.editMode = false;

                        this.alertService.success('Data ' + this.moduleName + ' has di updated');

                        this.loading = false;

                        this.clearForm();

                        this.modalHide();

                        this.loadData(this.currentPage);
                    },
                    (err) => {
                        this.alertService.error('failed Update data ' + this.moduleName);

                        this.loading = false;

                        let errorArr;

                        try {
                            errorArr = JSON.parse(err.error);
                        } catch (e) {
                            errorArr = err.error;
                        }

                        errorArr = errorArr.errors;

                        const errMsg = '';

                        Object.keys(errorArr).forEach((fieldName) => {
                            try {
                                this.htmlForm.form.controls[fieldName].setErrors(errorArr[fieldName][0]);
                            } catch (e) {
                                console.log(e);
                            }
                        });

                    }
                );
        } else {
            this.model.save(this.form)
                .subscribe(
                    (res: any) => {
                        this.alertService.success('Data ' + this.moduleName + ' data saved');

                        this.loading = false;

                        this.clearForm();
                        this.modalHide();                        

                        this.loadData(this.currentPage);
                    },
                    (err) => {

                        this.alertService.error('failed save data ' + this.moduleName);


                        this.loading = false;

                        let errorArr;

                        try {
                            errorArr = JSON.parse(err.error);
                        } catch (e) {
                            errorArr = err.error;
                        }

                        errorArr = errorArr.errors;

                        const errMsg = '';

                        Object.keys(errorArr).forEach((fieldName) => {
                            try {
                                this.htmlForm.form.controls[fieldName].setErrors(errorArr[fieldName][0]);
                            } catch (e) {
                                console.log(e);
                            }
                        });

                    }
                );
        }

        this.onAfterSave();

    }

    /**
     * Show Form Layout for Add Mode
     */
    addData(): void {
        this.showList = false;
        this.editMode = false;
		this.onAdd();
    }

    /**
     * Show Form Layout for Edit Mode
     */
    editData(entry): void {
        this.showList = false;
        this.editMode = true;

        if (this.detailModel) {
            this.form = entry;

            this.loadDetail(entry);
        } else {
            this.form = entry;
            console.log(this.form);

            this.onEdit();
        }

    }

    /**
     * Action on Edit
     */
    onAdd() { }
	
	/**
     * Action on Edit
     */
    onEdit() { }

    /**
     * Action on save
     */
    onBeforeSave() { }

    /**
     * Action on save
     */
    onAfterSave() { }

     /**
     * Amodal hide
     */
    modalHide() { }

    /**
     * Delete Module Data
     *
     * @param entry Object Data
     */
    deleteData(entry): void {

        const confirmed = confirm('are you sure to delete this data ' + this.moduleName + ' ini?');

        if (confirmed) {
            this.model.delete(entry.id)
                .subscribe(
                    (res: any) => {
                        this.loadData(this.currentPage);
                    },
                    (err) => {
                        this.alertService.error('failed delete data : ' + entry.nama);
                    }
                );
        }
    }

    /**
     * Add Data Detail
     */
    addDataDetail(event) {
        event.preventDefault();
        event.stopPropagation();

        // Validate
        if (!this.validateDetail()) {
            return;
        }

        this.beforeEditDetail();

        // Push Form Data to Data Detail Array
        if (!this.editDetailMode) {

            if (typeof this.formDetail.deleted_at !== 'undefined') {
                this.formDetail.deleted_at = null;
            }

            this.dataDetail.push(this.formDetail);
        }

        this.editDetailMode = false;

        // Reset Form Detail to Empty
        this.formDetail = Object.assign({}, this.moduleFormDetail);

        this.onAfterEditDetail();

        return false;
    }

    beforeEditDetail() { return true; }

    onAfterEditDetail() { return true; }

    /**
     * Edit Data Detail
     *
     * @param entry Row Array that need to manipulate
     */
    editDataDetail(entry): void {
        this.formDetail = entry;

        this.editDetailMode = true;
    }

    /**
     * Delete Data Detail
     *
     * @param entry Row Array that need to delete
     */
    deleteDataDetail(entry): void {
        if (typeof entry.id !== 'undefined' && entry.id !== '' && typeof entry.deleted_at !== 'undefined') {
            const dt = new Date().toJSON().slice(0, 19).replace('T', ' ');
            entry.deleted_at = dt;
            this.dataDetail.splice(this.dataDetail.indexOf(entry), 1);
        } else {
            this.dataDetail.splice(this.dataDetail.indexOf(entry), 1);
        }
    }

    /**
     * Reset Form Data
     */
    clearForm(): void {
        window.scrollTo(0, 0);

        this.form = Object.assign({}, this.moduleForm);

        if (this.detailModel) {
            this.formDetail = Object.assign({}, this.moduleFormDetail);
            this.dataDetail = [];
        }

        this.editMode = false;
        this.showList = true;
    }

    /**
     * Filter Data List
     */
    doSearch() {
        this.search = true;

        this.loadData(1);
    }

    /**
     * Clear Filter
     */
    clearSearch() {

        this.search = false;

        this.data = [];
        this.totalItems = 0;

        this.filter = Object.assign({}, this.moduleFilter);

        this.loadData(1);
    }

    /**
     * Load Module Data from API
     *
     * @param page Pagination page
     */
    loadData(page): void {
        this.loading = true;

        this.filter.page = page;

        this.model.get(this.filter)
            .subscribe(
                (res: any) => {
                    this.loading = false;

                    this.data = res.data;
                    this.totalItems = res.total;
                    this.currentPage = page;
                },
                (err) => {
                    this.loading = false;

                    if (err.status === 0) {
                        this.alertService.error('Tidak ada koneksi internet/Internet lambat');
                    } else {
                        this.alertService.error(err.message);
                    }
                }
            );
    }

    /**
     * Load Detail Data from API
     */
    loadDetail(entry) {
        this.detailModel.getDetail(entry.id)
            .subscribe((res: any) => {
                if (res.data.length > 0) {
                    this.dataDetail = res.data;
                    // this.dataDetail2 = res.data;
                }

                this.onEdit();
            }, (err) => {
                this.onEdit();
            });
    }

    /**
     * Load Module Dependencies Data
     */
    loadDependencies() { }

    /**
     * Validasi Data Form Master
     */
    validate() {
        return true;
    }

    /**
     * Validasi Data Form Detail
     */
    validateDetail() {
        return true;
    }

    /**
     * Enter Action
     *
     * @param event  DOM event
     * @param target element
     */
    keytab(event, target) {
        event.preventDefault();
        event.stopPropagation();

        const targetElm = target || null;

        if (targetElm && target !== '') {
            const elm = document.getElementById(target);

            if (elm.nodeName === 'INPUT' || elm.nodeName === 'SELECT' || elm.nodeName === 'TEXTAREA' || elm.nodeName === 'BUTTON') {
                elm.focus();
            } else {
                const elmNgSelect = document.getElementById(target).getElementsByTagName('input');

                if (typeof elmNgSelect[0] !== 'undefined') {
                    elmNgSelect[0].focus();
                }
            }

        } else {
            const element = event.srcElement.parentElement.nextElementSibling; //  get the sibling element

            if (event.srcElement.parentElement.nextElementSibling === null) {
                return;
            } else {
                element.childNodes[3].focus();   //  focus if not null
            }
        }

        return false;
    }

    /**
     * Open Url with additional filter session
     */
    openLink(filterData, redirectUrl) {
        this.session.setSession('filters', filterData);

        this.router.navigate([redirectUrl]);
    }

    /**
     * get data from popup
     * @param q data object formDetail
     */
    selectedCustomer(q) {
        this.form.customer_id   = q.id;
        this.form.customer_code = q.code;
        this.form.customer_name = q.name;
        // document.getElementById('perusahaan_id').getElementsByTagName('input')[0].focus();
    }



}
